<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <div class="p-4 card xl:p-5">
    <form
      @input="request.errors.clear($event.target.name)"
      @submit.prevent="submit()"
    >
      <div class="space-y-4">
        <div>
          <label for="textName">{{ $t("attributes.name") }}</label>
          <span class="relative flex mt-1.5">
            <input
              v-model="group.name"
              id="textName"
              name="name"
              type="text"
              class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 pl-9 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              autocomplete="off"
            />
            <span
              class="pointer-events-none absolute flex h-full w-10 items-center justify-center text-slate-400 peer-focus:text-primary dark:peer-focus:text-accent dark:text-navy-300"
            >
              <v-icon name="user-group" size="6"></v-icon>
            </span>
          </span>

          <validation-feedback>
            {{ request.errors.get("name") }}
          </validation-feedback>
        </div>

        <div
          class="rounded-lg border border-slate-200 p-4 dark:border-navy-600"
        >
          <div class="space-y-4">
            <div class="sticky flex gap-x-4 bg-white py-4 top-[61px] z-1">
              <div class="w-10 self-center">#</div>
              <div class="flex-1 font-medium">
                {{ $t("attributes.survey") }}
              </div>
              <div class="flex-1 font-medium">
                {{ $t("attributes.template") }}
              </div>
              <div class="flex-1 font-medium">
                {{ $t("attributes.survey_at") }}
              </div>
              <div class="w-10"></div>
            </div>
            <div
              v-for="(survey, index) in group.surveys"
              :key="index"
              class="flex gap-x-4"
            >
              <div class="w-10 self-center font-medium">{{ index + 1 }}.</div>
              <div class="flex-1">
                <input
                  v-model="survey.name"
                  :name="`survey${index}Name`"
                  :id="`textSurvey${index}Name`"
                  type="text"
                  class="h-10 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                />
              </div>
              <div class="flex-1">
                <vue-select
                  :options="templates.get()"
                  label="name"
                  :reduce="(template) => template.id"
                  v-model="survey.template_id"
                  :name="`survey${index}TemplateId`"
                ></vue-select>
              </div>
              <div class="flex-1">
                <input
                  :name="`survey${index}SurveyAt`"
                  :id="`dateSurvey${index}SurveyAt`"
                  :min="moment().add(1, 'day').format('YYYY-MM-DD')"
                  :value="survey.surveyAtISO"
                  type="date"
                  class="h-10 w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input peer hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
                  @input="survey.survey_at = $moment($event.target.value)"
                />
              </div>
              <div class="w-10 self-center">
                <v-button
                  color="transparent"
                  icon
                  :title="$t('labels.delete')"
                  @click="group.removeSurvey(index)"
                >
                  <i class="fa fa-lg fa-times"></i>
                </v-button>
              </div>
            </div>
            <div>
              <v-button
                color="transparent"
                block
                weight="normal"
                align="end"
                @click="group.addSurvey()"
              >
                <span class="mr-2">{{ $t("labels.add_survey") }}</span>
                <i class="fa fa-lg fa-plus"></i>
              </v-button>
            </div>
          </div>
        </div>

        <div class="text-end space-x-3">
          <v-button
            type="submit"
            color="primary"
            :disabled="!request.submittable"
          >
            {{ $t("labels.submit") }}
          </v-button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import Group from "@/models/Group";
import ModelRequest from "@/request/ModelRequest";
import TemplateCollection from "@/collections/TemplateCollection";
import moment from "moment";

export default {
  name: "GroupSurveysForm",
  computed: {
    moment() {
      return moment;
    },
  },
  props: {
    id: {
      type: [Number, String],
      required: false,
    },
  },
  data() {
    return {
      breadcrumbs: [
        { title: this.$t("titles.groups"), route: "admin.groups" },
        {
          title: this.$t("titles.surveys"),
          active: true,
        },
      ],
      templates: new TemplateCollection(),
      group: new Group(),
      request: new ModelRequest(),
    };
  },
  created() {
    this.templates.fetchAll();

    this.group.fetch(this.id);

    this.request.bind(this.group);
  },
  methods: {
    submit() {
      this.request.update();
    },
  },
};
</script>
